.Forms{
    position: relative;
}
.socratitBtn{
	background: #005395;
	border-radius: 3px;
	color: white;
	font-size: 16px;
	line-height: 19px;
	padding: 10px 32px;		
}
.socratitBtn:hover{
	background: #4A90c4;
	color: whitesmoke
}
.link.form-control {
    display: block;
    width: 100%;
    padding: 0px 0px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.links{
    position: relative;
}
.linksInput{
    display: block;
    width: 89%;
    height: calc(1.2em + .75rem + 2px);
    padding: 10px 10px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 0px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.linksInput:focus{
    border: 0px solid black;
    outline: 0px
}

.Massage{
    bottom: -40px;
}